* {
  box-sizing: border-box;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-weight: 300;
  }
  
  body {
    font-family: "Source Sans Pro", sans-serif;
    color: white;
    font-weight: 300;
   
    width: 100%;
    height: 100%;
  }
  
  .container {
    background: #f6d743;
    margin: 10% auto;
    padding: 80px 0;
    text-align: center;
    
  }
  
  .container h1 {
    font-size: 40px;
    font-weight: 200;
  }
  
  form {
    padding: 20px 0;
    position: relative;
  }
  form input {
    border: 1px solid rgba(252, 252, 252, 0.4);
    background-color: rgba(252, 252, 252, 0.2);
    width: 250px;
    border-radius: 3px;
    font-family: "Source Sans Pro", sans-serif;
    padding: 10px 15px;
    margin: 0 auto 10px auto;
    display: block;
    text-align: center;
    font-size: 18px;
    color: white;
    font-weight: 300;
  }
  
  form button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: 0;
    background-color: white;
    border: 0;
    padding: 10px 15px;
    color:black;
    border-radius: 3px;
    width: 250px;
    font-size: 18px;
  }

  .welcome{
    background: url(/static/media/schoolbus.55ff44ab.gif);
    background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 100% 100%;
  height: 100vh;
 

  }
  
